.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 20px;

  @include mobile {
    grid-template-columns: repeat(2 1fr);
    column-gap: 30px;
  }

  &--desktop {
    display: grid;

    @include mobile {
      display: block;
    }

    @include tablet {
      display: block;
    }

    @include notebook {
      display: grid;
    }
  }

  &__item {
    @for $start from 1 through 12 {
      @for $end from $start through 12 {
        &--#{$start}-#{$end} {
          grid-column: #{$start} / #{$end + 1};
        }

        &--largescreens--#{$start}-#{$end} {
          grid-column: #{$start} / #{$end + 1};
        }
      }
    }
  }
}